import api from '~api'
import config from '~api-config'

let addVip = (params) => api.post('/member/create.action',config.apiVip,params);//
let editVip= (params) => api.post('/member/update/wechat.action',config.apiVip,params);//openid
let updateVip= (params) => api.post('/member/update.action',config.apiVip,params);//
let deleteVip= (params) => api.post('/member/cancel.action',config.apiVip,params);//
let vipList= (params) => api.post('/member/list.action',config.apiVip,params);//(storeId)
let allVipList= (params) => api.post('/member/listall.action',config.apiVip,params);//

export default {
    addVip,
    editVip,
    updateVip,
    deleteVip,
    vipList,
    allVipList
}



